body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.form {
	padding: 5vh 30vw;
}

.home > h1,
p {
	display: flex;
	align-items: center;
	justify-content: center;
}

.google-login-button {
	display: flex;
	justify-content: center;
	padding: 5%;
	margin-top: 2%;
}

.login-text {
	display: flex;
	justify-content: center;
}

.form > * {
	padding: 4%;
}

.submit-button {
	display: flex;
	justify-content: center;
}

#hr-tag-login {
	display: flex;
	justify-content: center;
	align-items: center;
}

.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.2);

	display: flex;
	justify-content: center;
	align-items: center;
}

.break-img {
	background-color: rgba(41, 39, 39, 0.1);
	margin-left: 2vw;
	margin-right: 2vw;
}

.navbar {
	display: flex;
}

.shopping-cart {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	margin-left: 0;
}

.explore-grid {
	padding: 20px 20px;
}

.explore-hr,
.cart-hr {
	margin-left: 10%;
	margin-right: 10%;
}

.explore-hr > hr,
.cart-hr > hr {
	display: flex;
	background-color: rgba(0, 0, 0, 0.2);
	height: 2px;
}

.user-dashboard-body-text {
	display: flex;
	justify-content: center;
	margin-top: 20vh;
}

.user-dashboard-buttons {
	display: flex;
	justify-content: space-evenly;
	margin-top: 5vh;
}

.delete-button-cart {
	display: flex;
	align-self: center;
	margin-left: 2px;
}

.product-details > h1 {
	margin-bottom: 4%;
}

.qty-update-box {
	display: flex;
	box-shadow: 1px 2px 3px 1px #8a9191;
	border-radius: 20px;
}

.admin-navbar {
	display: flex;
}

.admin-dashboard {
	display: flex;
	margin: 7% 26%;
	box-shadow: rgba(0, 0, 0, 0.2) 0px 18px 50px -10px;
}

.admin-dashboard-heading {
	display: flex;
	justify-content: center;
}

.new-line {
	white-space: pre-line;
}
.admin-dashboard-text {
	margin: 2% 5% 5% 5%;
}

.search-box{
	display: flex;
	justify-content: center;
	margin: 1% 1%;
}

.category-details > * {
	padding-left: 20%;
	padding-bottom: 2%;
}

.update-button-category {
	margin-bottom: 5%;
}